import React, { useState } from 'react';
import Btn from './components/Btn/Btn';
import './App.css';


function App() {
  const [expression, setExpression] = useState('');
  const [result, setResult] = useState('');

  const handleButtonClick = (value) => {
    // Cette fonction sera appele lors du click sur un bouton avec la valeur "value".
    console.log('Button clicked with value:', value);
    if (value === '=') {
      try {
  
          const evalResult = evaluateExpression(expression);//evalue l'expression
        setResult(evalResult.toString()); //Met  jour le rsultat
        setExpression(''); //Rinitilise l'expression
      } catch (error) {
        setResult('Erreur');
      }
    } else {
       setExpression((prevExpression) => prevExpression + value);
    }
   };

   const evaluateExpression = (expr) => {
     // Implmentez votre propre logique d'valuation ici
    // Vous pouvez utiliser des bibliothques tierces pour vous aider dans cette tche
    // Assurez-vous de grer correctement les erreurs et les oprations mathmatiques

    // Exemple simple en utilisant eval (ceci est pour dmonstration seulement)
    return eval(expr);
     
     
   };

  
  return (
    <div className="App">
      <header className="App-header">
          <h1>Calculatrice</h1>
         </header>
      
        <div className='App-body'>
   
        <input
          type='text'
          className='expression-input'
          value={expression}
          readOnly={true}
        />
         <Btn onClick={handleButtonClick} />
      
          <input
            type='text'
            className='resultat-input'
            value={result}
            readOnly={true}
          />     
        
        
        </div>
    </div>
  );
}

export default App;
